import { FC, useState } from "react"
import { Button, DatePicker, Form, Input, Select } from "antd"
import { DownOutlined } from "@ant-design/icons"
import { ECountry, countries, russianCities, usaCities } from "./constants"

import "./App.css"

const { Option } = Select

interface IFormValue {
  name: string
  surname: string
  middlename: string
  birthday: moment.Moment
  country: string
  city: string
}

const App: FC = () => {
  const [selectedCountry, setSelectedCountry] = useState<ECountry>()
  const [form] = Form.useForm<IFormValue>()

  const onFinish = (value: IFormValue) => {
    const info = {
      client: {
        name: value.name,
        surname: value.surname,
        middlename: value.middlename,
        birthday: value.birthday
          ? value.birthday.format("YYYY-MM-DD")
          : undefined,
      },
      place: {
        country: value.country,
        city: value.city,
      },
    }
    console.log(info)
  }

  return (
    <div className="App">
      <Form
        name="basic"
        labelCol={{ span: 8 }}
        wrapperCol={{ span: 16 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        autoComplete="off"
        form={form}
      >
        <Form.Item
          label=""
          name="surname"
          rules={[
            {
              required: true,
              message: ",  !",
              type: "string",
            },
            {
              message: "   ",
              pattern: /^[a-zA-Z--]*$/,
            },
          ]}
        >
          <Input placeholder=" " className="input" />
        </Form.Item>

        <Form.Item
          label=""
          name="name"
          rules={[
            {
              message: "   ",
              pattern: /^[a-zA-Z--]*$/,
            },
          ]}
        >
          <Input placeholder=" " className="input" />
        </Form.Item>

        <Form.Item
          label=""
          name="middlename"
          rules={[
            {
              message: "   ",
              pattern: /^[a-zA-Z--]*$/,
            },
          ]}
        >
          <Input placeholder=" " className="input" />
        </Form.Item>

        <Form.Item label=" " name="birthday">
          <DatePicker className="input" placeholder=" " />
        </Form.Item>

        <Form.Item label=" " name="country">
          <Select
            suffixIcon={
              <div>
                <DownOutlined />
              </div>
            }
            placeholder=" "
            className="input"
            onChange={(value: ECountry) => {
              form.resetFields(["city"])
              setSelectedCountry(value)
            }}
          >
            {countries?.map((item) => (
              <Option key={item.id} value={item.value}>
                {item.value}
              </Option>
            ))}
          </Select>
        </Form.Item>

        <Form.Item label=" " name="city">
          <Select
            suffixIcon={
              <div>
                <DownOutlined />
              </div>
            }
            placeholder=" "
            className="input"
          >
            {selectedCountry === ECountry.Russia &&
              russianCities?.map((item) => (
                <Option key={item.id} value={item.value}>
                  {item.value}
                </Option>
              ))}
            {selectedCountry === ECountry.USA &&
              usaCities?.map((item) => (
                <Option key={item.id} value={item.value}>
                  {item.value}
                </Option>
              ))}
          </Select>
        </Form.Item>

        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  )
}

export default App
